



/* faqs.css */
.accordion {
  width: 100%;
  max-width: 1000px;
  margin-top: 50px;
}

.accordion-item {
  border: 1px solid #ddd;
  border-top: none;
  margin-bottom: 10px;
}

.accordion-item-header {
  background-color: #f5f5f5;
  padding: 10px;
  cursor: pointer;
  font-weight: bold;
}

.accordion-item-body {
  overflow: hidden;
  transition: height 0.3s ease-in-out;
  height: 0;
}

.accordion-item-body.is-open {
  height: auto;
}

.accordion-item-body-content {
  padding: 10px;
}


